@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir-Book.ttf);
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir-Heavy.ttf);
  font-weight: 900;
}

* {
  box-sizing: border-box;
}

body {
  font-family: Avenir, sans-serif;
  font-weight: 300;
  
  --big-search-bar-height: 70px;
  --small-search-bar-height: 42px;
}

.search-form {
  position: relative;
  margin-top: 20px;
}

/* Label::before styles */

.search-label::before {
  content: "";
  position: absolute;

  left: 26px;
  top: 25px;
  width: 19px;
  height: 19px;

  background-image: url(./images/Search.svg);
}

.search-label--small::before {
  left: 13px;
  top: 15px;
  width: 11px;
  height: 11px;
  background-size: contain;
}

/* Input :default styles */

.search-bar {
  width: 100%;
  height: var(--big-search-bar-height);
  padding-left: 62px;

  font-family: Avenir, sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #3d4e61;

  background: #fff;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px #3d4e611a;
  border-radius: 4px;
}

.search-bar--small {
  height: var(--small-search-bar-height);
  padding-left: 33px;

  font-size: 14px;
  line-height: 19px;
}

/* Input :hover and :focus*/

.search-bar:hover {
  outline: 1px solid #e1e7ed;
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-bar:focus {
  outline: 1px solid #e1e7ed;
  border-radius: 3px;

  font-family: Avenir, sans-serif;
  font-weight: 900;

  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
}
